/**
 * 推荐人填写暂存
 **/
import React, {useState} from 'react';
import Button from 'element-react/dist/npm/es5/src/button';
import Input from 'element-react/dist/npm/es5/src/input';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {transfer, updateUnformedUpstreamPlayer} from '../../actions';
import './MsgBox.scss';
import {checkedSendEosName} from '../../Utils/utils';
import {getReadOnlyEOS} from "../../networks/index";

const RoundRankList = (props) => {
  const [value, setValue] = useState(props.unformedUpstreamPlayer);
  const [error, setError] = useState('');

  const saveUpstream = async () => {
    if (!checkedSendEosName(value)) {
      setError('specification');
      return;
    }

    let isExists = false;

    await getReadOnlyEOS().getAccount(value)
      .then((res) => {
        console.log(res, 7654321);
        isExists = true;
      }).catch( (err) => {
        console.log(err, '1234567');
        isExists = false
    });

    if (!isExists) {
      setError('nonentity');
      return;
    }

    props.updateUnformedUpstreamPlayer(value);
    props.close(true);
  };

  const onchange = (val) => {
    setValue(val);
    setError('')
  };

  return (
    <div className="modal-content-up-bind">
      <i className="el-icon-close modal-btn-close" onClick={props.close} />
      <p className="modal-up-bind-title">填写推荐人</p>
      <Input
        type='text'
        placeholder="请填写推荐人"
        value={value}
        onChange={(val) => onchange(val)}
        style={{border: error === '' ? '1px solid #C0C4CC' : '1px solid red'}}
      />
      <p className="bind-prompt-input-error">{error === '' ? '' : error === 'specification' ? 'EOS账号格式错误' : 'EOS账号不存在'}</p>
      <Button
        className="modal-up-bind-cancel"
        onClick={() => props.close(true)}
      >
        取消
      </Button>
      <Button
        className="modal-up-bind-save"
        onClick={saveUpstream}
      >
        保存
      </Button>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    unformedUpstreamPlayer: state.unformedUpstreamPlayer,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer, updateUnformedUpstreamPlayer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(RoundRankList);

